import { Component, OnInit, Injector, HostListener, ViewChild } from '@angular/core';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';
import * as moment from 'moment';
import * as _ from 'underscore';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router } from '@angular/router';

@Component({
  selector: 'app-my-visitor',
  templateUrl: './my-visitor.component.html',
  styleUrls: ['./my-visitor.component.scss']
})

export class MyVisitorComponent implements OnInit {
  
  visitorData:string = "";
  visitorListData:any;

  isVisitorDataLoaded:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  visitor:any;

  modalService:any;

  totalFilterItems:any;

  apartmentUnitId:any;

  visitTypeData:any;
  listData: any;
  columnData:any;
  gridSourceData:any;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    private router: Router,
    private injector: Injector,
  	private apartmentService:ApartmentService,
    private visitorService:VisitorService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService: CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  getVisitType(id){

  	 var data = _.filter( this.visitTypeData, function( item ) {
      if(item.lookupValueId === id)
        return item;
    } );

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName.toLowerCase();
    }

  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'MyVisitorData');
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  @HostListener('window:showConfirmModal', ['$event.detail'])
  showConfirmModal(detail) {
    this.modalService.showConfirmModal(detail.rowId);
  }

  @HostListener('window:navigateTo', ['$event.detail'])
  navigateTo(detail) {

    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let expectedVisitorId = dataRecord.expectedVisitorId
    this.router.navigateByUrl('/user/visitor/edit-visitor/' + expectedVisitorId);

  }

  getDateTime(val){
    if(val != ''){
      return moment(val).format("MM/DD/YY hh:mm")
    }else{
      return ''
    }
  }

  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
         text: 'Id',
         datafield: 'serialNo',
         width: 100,
         pinned: true,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
      }, {
          text: 'Visitor Name',
          datafield: 'expectedVisitorName',
          width: 100,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      },{
         text: 'Phone Number',
         datafield: 'expectedVisitorPhone',
         cellsrenderer: cellsrenderer,
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'In-time',
         datafield: 'expectedVisitorInTime',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ this.getDateTime(value) +'</div>'
        },
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Out-time',
         datafield: 'expectedVisitorOutTime',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ this.getDateTime(value) +'</div>'
        },
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Visit Type',
         datafield: 'visitTypeId',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ this.getVisitType(value) +'</div>'
        },
         minwidth: 80,
         renderer: columnrenderer
     },{
      text: 'Actions',
      cellsalign: 'center', 
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="navigateTo('+ row +')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="showConfirmModal('+ row +')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a></div>'
      },
      renderer: columnrenderer
  }];
     
    let apartmentUnitParams = {
      userId: parseInt(this.cookieService.get('userId'))
    }

  	this.apartmentService.getApartmentBlockUnitByUserId(apartmentUnitParams).subscribe((res:any) => {

    this.apartmentUnitId = res[0].apartmentBlockUnitId;
    
    let params = {
      apartmentUnitId: this.apartmentUnitId
    }
    console.log(this.apartmentUnitId);

		this.visitorService.getExpectedVisitorsByApartmentUnitId(params).subscribe((res:any) => {

        //filter active true items
        this.visitorListData = res.filter(data => {
          return data.isActive
        })

        console.log(res);

        this.gridSourceData = {
          localdata: this.visitorListData,
          datatype: "array"
        }
    
        this.listData = new jqx.dataAdapter(this.gridSourceData);

        this.totalItems = this.visitorListData.length;

        if(this.totalItems>this.itemLimit){
          this.ItemEndIndex = this.itemLimit;
        }

        else {
          this.ItemEndIndex = this.totalItems;
        }

        this.isVisitorDataLoaded = true;

      }, error => {

    });

	},
	error => {

	}
  );
  
  let visitParams = {
    LookupTypeId: 15
  }

  	//get visit type
    this.lookupService.getLookupValueByLookupTypeId(visitParams).subscribe((res:any) => {

      this.visitTypeData = res;

    },
    error => {
      
    }
    );

    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe( index => {

      if(index != null){


        var expectedVisitorId = this.visitorListData[index].expectedVisitorId;

        var params = {
            expectedVisitorId: expectedVisitorId,
            deleteBy: parseInt(this.cookieService.get('userId'))
        }

      

        this.isVisitorDataLoaded = false;

        this.visitorService.deleteExpectedVisitorById(params).subscribe((res:any) => {

          this.visitorListData.splice(index, 1);
          this.totalItems = this.visitorListData.length;
          this.isVisitorDataLoaded = true;
          this.sharedService.setUnitListDeleteIndex(null);

        }, error => {
          console.log(error);
        });
        
        
      }

    } )
  	

  }

}

function navigateTo(row){
  var event = new CustomEvent('navigateTo', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).navigateTo = navigateTo

function showConfirmModal(row){
  var event = new CustomEvent('showConfirmModal', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmModal = showConfirmModal